import { Store } from "../helpers/sbs-store";
import { PlayerStates } from "../helpers/utils";
import { Messengers } from "../helpers/Messengers";

const AppVersion = Store.appVersion;
const majorVersion = AppVersion.split(".")[0] || "0";
const baseUrl = Store.baseApiUrl;

class AdobeAnalytics {
    originalEnvironmentData = {
        browser: {
            userAgent: navigator.userAgent,
        },
        platform: {
            appBuild: "1",
            appName: "on demand ctv",
            appVersion: AppVersion,
            deviceLanguage: "en",
            deviceName: "foxtel fac",
            deviceType: "CTV",
            firmware: "<empty>", // populate on init
            firmwareYear: "",
            resolution: "1920 X 1080",
            installDate: Store.getAppInstallDate(),
            modelNumber: "<empty>", // populate on init
            playerName: "stb.foxtelFac.v3",
        },
    };

    environmentData = this.originalEnvironmentData;

    originalUserData = {
        userInfo: {
            userID: "",
            loggedIn: false,
        },
    };
    userData = this.originalUserData;

    originalPageData = {
        attributes: {
            content: {
                title: "",
                program: "",
            },
        },
        category: {
            primaryCategory: "ondemand",
            siteSubSection1: "MLELanding",
            siteSubSection2: "",
            siteSubSection3: "",
            siteSubSection4: "",
        },
        pageInfo: {
            clickSource: {},
            campaign: {},
            destinationURL: location.origin + location.pathname,
            language: "en",
            originalReferrerURL: "",
            pageID: "",
            pageName: "s:ondemand:ctv:MLELanding",
            previousPageName: "",
            previousPageURL: "",
            referralURL: "",
            siteName: `ondemand:ctv.foxtelmle.v1`,
            title: "",
        },
    };
    pageData = this.originalPageData;

    videoData = null;

    hasPlayed = false;
    currentAd = null;
    chapterNumber = 0;

    libraryLoaded = false;
    listeners = [];

    constructor() {
        this.prepareWindowOnInit();
        this.loadEnsightenScript();
    }

    init = () => {
        this.originalEnvironmentData.platform.modelNumber = Store.deviceModel;
        this.originalEnvironmentData.platform.firmware =
            Store.deviceFirmwareVersion;
        this.environmentData = this.originalEnvironmentData;
        this.removeEventListeners(); // in case monitoringTeardown isn't called
        this.prepareWindowOnInit();
        this.addListeners();
    };

    prepareWindowOnInit = () => {
        this.updateWindowStaticProperties();
        // this.addEvent("appState", "appStarted");
        // for some reason the library's sub-library errors if we don't set these
        // not sure if we can ignore but we add them to be safe
        window.ctvEnv = "dev";
        window.ctvMake = Store.deviceMake;
    };

    addListeners = () => {
        this.listeners = [
            ...this.listeners,
            Messengers.streamReady$.addReceiver(
                this.onChannelDataReceived,
                true
            ),
            Messengers.playerState$.addReceiver(this.onPlayerStateChange),
            Messengers.currentAd$.addReceiver(this.onAdChange),
            Messengers.appTeardown$.addReceiver(this.appTeardown),
        ];
    };

    removeEventListeners = () => {
        this.listeners.forEach((receiver) => receiver.unlisten());
        this.listeners = [];
    };

    appTeardown = () => {
        this.removeEventListeners();
        this.hasPlayed = false;
        this.currentAd = null;
        this.pageData = this.originalPageData;
        this.environmentData = this.originalEnvironmentData;
        this.userData = this.originalUserData;
        this.chapterNumber = 0;
        this.videoData = null;
        console.log("[acc] Cleaned up adobe analytics");
    };

    // window variables need to be set before we load the SDK, so we load here
    loadEnsightenScript = async () => {
        try {
            if (!this.libraryLoaded) {
                // if (Store.useProdConfig) {

                // always load prod sdk - staging sdk throws errors and doesn't send some events...
                await import("../lib/ensighten-sdk-prod.js");

                // } else {
                //     await import("../lib/ensighten-sdk-stag.js");
                // }
            }
            this.libraryLoaded = true;
        } catch (err) {
            console.error("[acc] error loading adobe sdk", err);
        }
    };

    onChannelDataReceived = async (data) => {
        if (!data) return;

        if (!Store.appIsRunning) return; // in case we teardown while script is loading

        this.userData = {
            userInfo: {
                userID: Store.memberId || "<empty>",
                loggedIn: true,
            },
        };
        this.pageData = {
            ...this.pageData,
            category: {
                ...this.pageData.category,
                siteSubSection1: "video",
                siteSubSection2: data.mpxMediaId,
            },
            pageInfo: {
                ...this.pageData.pageInfo,
                pageName: `s:ondemand:ctv:video:${data.mpxMediaId}`,
                previousPageName: "s:ondemand:ctv:MLELanding",
            },
        };
        this.updateWindowStaticProperties();

        const videoItem = data.videoItem;
        if (videoItem?.name) {
            const videoId = videoItem.id.substring(
                videoItem.id.lastIndexOf("/") + 1
            );
            let pilatId = "0";
            if (typeof videoItem.pilat === "string") {
                pilatId = pilatId.substring(pilatId.lastIndexOf("/") + 1);
            } else if (videoItem.pilat?.id) {
                pilatId = videoItem.pilat.id;
            }

            const taxonomy = {
                genre: [],
                subGenre: [],
                collection: [],
                theme: [],
                tone: [],
                ...videoItem.taxonomy,
            };

            const videoType = taxonomy.useType || videoItem.type || "";

            let ccEnabled = "off";

            this.videoData = {
                videoInfo: {
                    videoName: videoItem.name,
                    mpxID: videoId,
                    pilatID: pilatId,
                    duration: videoItem.duration || null,
                    player: this.originalEnvironmentData.platform.playerName,
                    mediaPart: `${this.chapterNumber}.0`,
                    subtitle: ccEnabled,
                    streamType: "live",
                },
                attribute: {
                    contentType: `video:${videoType.toLowerCase()}`,
                    programName: videoItem.partOfSeries?.name ?? videoItem.name,
                    channel: taxonomy.channel?.[0].name || "",
                    episodeNumber: videoItem.episodeNumber || "",
                    episodeTitle:
                        videoItem?.displayTitles?.episodePromotion?.title || "",
                    seasonNumber: videoItem.partOfSeason
                        ? videoItem.partOfSeason.seasonNumber
                        : "",
                    language:
                        videoItem.inLanguage?.map((lang) => lang.name) || [],
                    countryOfOrigin: videoItem.country
                        ? videoItem.country.name
                        : "",
                    classification: videoItem.contentRating || "",
                    awardNomination: "",
                    genre: taxonomy.genre?.map((val) => val.name) || [],
                    subGenre: taxonomy.subGenre?.map((val) => val.name) || [],
                    collection:
                        taxonomy.collection?.map((val) => val.name) || [],
                    category: "",
                    theme: taxonomy.theme?.map((val) => val.name) || [],
                    tone: taxonomy.tone?.map((val) => val.name) || [],
                    keywords: "",
                    subject: "",
                },
            };

            this.addEvent("videoMilestone", "videoLoaded", {
                environment: this.environmentData,
                user: this.userData,
                page: this.pageData,
                video: this.videoData,
            });
        }
    };

    addEvent = (eventName, action, additionalInfo) => {
        if (additionalInfo?.video?.videoInfo) {
            additionalInfo.video.videoInfo.mediaPart = `${this.chapterNumber}.0`;
        }
        const newEvent = {
            additionalInfo,
            event: eventName,
            eventAttribute: {
                action,
            },
        };
        if (Array.isArray(window.digitalData.events)) {
            window.digitalData.events.push(newEvent);
            console.log("[acc] Adobe pushed new event", eventName, action);
        }
    };

    updateWindowStaticProperties = () => {
        window.digitalData = {
            events: window.digitalData?.events || [],
            page: this.pageData,
            user: this.userData,
            environment: this.environmentData,
            version: "1.0",
            sbsVersion: {
                variant: "ODCTV",
                version: "1.0",
                dataLayerLibrary: AppVersion,
            },
        };
    };

    onPlayerStateChange = (playerState) => {
        if (playerState === PlayerStates.Playing && !this.hasPlayed) {
            this.addEvent("videoMilestone", "videoStart", {
                page: this.pageData,
                user: this.userData,
                environment: this.environmentData,
                video: this.videoData,
            });
            this.hasPlayed = true;
        }
    };

    buildAdEvent = (ad) => ({
        adInfo: {
            adId: ad.adId,
            adTitle: ad.title || "",
            creativeDurationInSeconds: ad.duration || -1,
            creativeId: ad.creativeAdId || "",
        },
    });

    onAdChange = (ad) => {
        const prevAd = this.currentAd;
        this.currentAd = ad;
        console.log("Adobe ad change", ad);
        if (prevAd) {
            const prevAdEvent = this.buildAdEvent(prevAd);
            this.addEvent("adEvents", "adCompleted", {
                page: this.pageData,
                user: this.userData,
                video: this.videoData,
                environment: this.environmentData,
                ad: prevAdEvent,
            });
        }
        if (this.currentAd) {
            const newAdEvent = this.buildAdEvent(this.currentAd);
            this.addEvent("adEvents", "adStarted", {
                page: this.pageData,
                user: this.userData,
                video: this.videoData,
                environment: this.environmentData,
                ad: newAdEvent,
            });
        }

        if (prevAd && !this.currentAd) {
            // Ad break end
            this.chapterNumber += 1;
            this.addEvent("videoMilestone", "videoChapterStarted", {
                user: this.userData,
                page: this.pageData,
                environment: this.environmentData,
                video: this.videoData,
            });
        }
    };
}

export default new AdobeAnalytics();
