import { Messengers } from "../helpers/Messengers";

class LiveGoogleDai {
    currentAd = null;
    currentTime = -1;

    adPollInterval = null;
    currentLiveAdBreakData = null;
    trackingUrl = "";

    futureStreamEvents = [];
    listeners = [];

    init = () => {
        this.removeListeners(); // remove in case monitoringTeardown isn't called
        this.addListeners();
    };

    addListeners = () => {
        this.listeners = [
            ...this.listeners,
            Messengers.timeUpdate$.addReceiver(this.onTimeUpdate),
            Messengers.googleDaiResponse$.addReceiver(
                this.onDaiResponseUpdated,
                true
            ),
            Messengers.id3Metadata$.addReceiver(this.onID3TagDataReceived),
            Messengers.timedMetadata$.addReceiver(this.onTimedMetadataEvent),
            Messengers.currentAd$.addReceiver(this.onAdChange),
            Messengers.appTeardown$.addReceiver(this.onAppTeardown),
        ];
    };

    removeListeners = () => {
        this.listeners.forEach((receiver) => receiver.unlisten());
        this.listeners = [];
    };

    onAppTeardown = () => {
        if (this.adPollInterval) {
            clearInterval(this.adPollInterval);
        }
        this.removeListeners();
    };

    onAdChange = (ad) => {
        if (ad) {
            console.log(
                `[acc] ad change - ${
                    ad.isSlate ? "slate" : ad.positionInBreak
                }/${ad.totalInBreak} (${ad.duration}s)`
            );
        } else {
            console.log(`[acc] ad break over`);
        }
        this.currentAd = ad;
    };

    sendTrackingEvent = (id) => {
        if (this.trackingUrl && id) {
            fetch(`${this.trackingUrl}${id}`).catch();
        }
    };

    checkForLiveAd = () => {
        this.futureStreamEvents.sort(
            (ev1, ev2) => ev1.timestamp - ev2.timestamp
        );
        let nextTag = this.futureStreamEvents[0];
        while (nextTag?.timestamp < this.currentTime) {
            this.futureStreamEvents.shift();
            if (nextTag.type === "id3Tag") {
                const googleData = this.matchTagToAd(nextTag);
                if (googleData) {
                    const { trackingEvent, ad } = googleData;
                    if (trackingEvent) {
                        // console.log(
                        //     "[acc] ad tracking event",
                        //     trackingEvent.name
                        // );
                        this.sendTrackingEvent(trackingEvent.trackingId);
                    }
                    if (ad && !this.areTheseTheSameAd(this.currentAd, ad)) {
                        Messengers.currentAd$.send(ad);
                    }
                } else if (this.currentAd) {
                    // content id3 tag so we assume this wont happen in ad breaks and can
                    // exit ad break. Alternative way is to rely on CUE-IN tag (below)
                    Messengers.currentAd$.send(null);
                }
            } else if (nextTag.type === "cueIn") {
                if (this.currentAd) {
                    // may not have content id3 tags so may need to rely on CUE-IN
                    // tag in manifest
                    Messengers.currentAd$.send(null);
                }
            }

            nextTag = this.futureStreamEvents[0];
        }
    };

    onTimeUpdate = (time) => {
        this.currentTime = time;
        this.checkForLiveAd();
    };

    areTheseTheSameAd = (ad1, ad2) => {
        if (!ad1 && !ad2) return true;
        if (!ad1 || !ad2) return false;
        return (
            ad1.adBreakId === ad2.adBreakId &&
            ad1.positionInBreak === ad2.positionInBreak
        );
    };

    matchTagToAd = ({ rawTag, tagTimestamp }) => {
        const googleIdx = rawTag.indexOf("google");
        if (googleIdx > -1 && this.currentLiveAdBreakData) {
            const googleTag = rawTag.slice(googleIdx);
            const matchingKey = Object.keys(
                this.currentLiveAdBreakData.tags
            ).find((key) => googleTag.startsWith(key));

            if (matchingKey) {
                const adKey = this.currentLiveAdBreakData.tags[matchingKey]?.ad;
                if (adKey && this.currentLiveAdBreakData.ads[adKey]) {
                    const matchingAd = this.currentLiveAdBreakData.ads[adKey];
                    const matchingAdBreak =
                        this.currentLiveAdBreakData.ad_breaks[
                            matchingAd.ad_break_id
                        ];

                    const formattedAd = {
                        adId: matchingAd.ad_id || "",
                        adBreakId: matchingAd.ad_break_id,
                        positionInBreak: matchingAd.position,
                        duration: matchingAd.duration,
                        totalInBreak: matchingAdBreak.ads,
                        adBreakDuration: matchingAdBreak.expected_duration,
                        title: matchingAd.slate ? "Slate ad" : matchingAd.title,
                        isSlate: matchingAd.slate,
                        creativeAdId: matchingAd.creative_ad_id,
                    };

                    const tagType =
                        this.currentLiveAdBreakData.tags[matchingKey].type;

                    let trackingEvent;
                    if (tagType !== "progress") {
                        trackingEvent = {
                            time: tagTimestamp,
                            trackingId: googleTag,
                            name: tagType,
                        };
                    }

                    return {
                        ad: formattedAd,
                        trackingEvent,
                    };
                }
            }
        }
    };

    onID3TagDataReceived = (aampId3Data) => {
        const { presentationTime, timeScale, data } = aampId3Data.detail;
        const id3TagTime = presentationTime / timeScale;

        let rawTag = "";
        data.forEach((charCode) => {
            const char = String.fromCharCode(charCode);
            rawTag += char;
        });
        const id3Data = {
            rawTag,
            timestamp: id3TagTime,
            type: "id3Tag",
        };
        this.futureStreamEvents.push(id3Data);
        this.checkForLiveAd();
    };

    onTimedMetadataEvent = (aampTimedMetadataEvent) => {
        if (!aampTimedMetadataEvent?.detail?.timedMetadata?.name) return;

        const {
            detail: {
                timedMetadata: { time: timestampMs, content, name },
            },
        } = aampTimedMetadataEvent;

        // only do ad break end cue in events
        // We may not get 'complete' tracking events (on slate) or content ID3 tags
        // so this is the only other way we gather whether an ad break is done
        // For ad break start we can rely on presence of google ID3 tags
        if (
            (name === "#EXT-X-CUE" && content === "IN") ||
            name === "#EXT-X-CUE-IN"
        ) {
            this.futureStreamEvents.push({
                timestamp: timestampMs / 1000,
                type: "cueIn",
            });
        }
    };

    checkServerAds = (pollUrl) => {
        fetch(pollUrl)
            .then((res) => res.json())
            .then((res) => {
                this.currentLiveAdBreakData = res;
                this.checkForLiveAd();
            })
            .catch((err) => {
                console.error("Error on google dai ad poll check", err);
            });
    };

    onDaiResponseUpdated = (daiResponse) => {
        if (!daiResponse) return;
        this.trackingUrl = daiResponse.media_verification_url;
        if (this.adPollInterval) clearInterval(this.adPollInterval);

        this.checkServerAds(daiResponse.metadata_url);
        this.adPollInterval = window.setInterval(
            () => this.checkServerAds(daiResponse.metadata_url),
            (daiResponse.polling_frequency || 10) * 1000
        );
    };
}

export default new LiveGoogleDai();
