import { Messengers } from "../helpers/Messengers";
import { Store } from "../helpers/sbs-store";
import { PlayerStates, NetworkConnectionType } from "../helpers/utils";

const AppVersion = Store.appVersion;
const ConvivaPlayerName = `ctv.foxtellinear.v0`;

const convivaProdConstants = {
    key: "b800410803a4659ffcd7b9b6577a907110b7aa21",
    gatewayUrl: "https://cws.conviva.com",
    debug: false,
};
const convivaStageConstants = {
    key: "d793177943f56c9e4795b0ba62b873c61132016f",
    gatewayUrl: "https://sbs-test1.testonly.conviva.com",
    debug: true,
};
const convivaConstants = Store.useProdConfig
    ? convivaProdConstants
    : convivaStageConstants;

class ConvivaBridge {
    conviva = null;
    videoAnalytics = null;
    clientSettings = {};

    currentAd = null;
    adBreakCounter = 0;
    streamProviderIsGoogleDAI = false;
    contentTags = {};

    listeners = [];

    init = async () => {
        if (!this.conviva) {
            try {
                window.Conviva = await import("../lib/conviva");
            } catch (e) {
                console.error("[acc] could not import conviva", e);
            }
            if (!Store.appIsRunning) return; // in case we teardown while script is loading
            if (window.Conviva) {
                this.conviva = window.Conviva;
            } else {
                console.warn("[acc] No Conviva on window");
                return;
            }
            this.clientSettings = new this.conviva.ClientSettings(
                convivaConstants.key
            );
            this.clientSettings.gatewayUrl = convivaConstants.gatewayUrl;
            // clientSettings.logLevel = 0;
        }

        this.conviva.Analytics.init(
            convivaConstants.key,
            null,
            this.clientSettings
        );
        this.videoAnalytics = this.conviva.Analytics.buildVideoAnalytics();

        // Set Device Metadata - should be called after Conviva.Analytics.init
        const deviceMetadata = {};
        deviceMetadata[Conviva.Constants.DeviceMetadata.BRAND] = "Foxtel";
        deviceMetadata[Conviva.Constants.DeviceMetadata.MANUFACTURER] = "Foxtel";
        deviceMetadata[Conviva.Constants.DeviceMetadata.MODEL] = Store.deviceModel;
        deviceMetadata[Conviva.Constants.DeviceMetadata.TYPE] = Conviva.Client.DeviceType.SETTOP;
        deviceMetadata[Conviva.Constants.DeviceMetadata.VERSION] = "n/a";
        deviceMetadata[Conviva.Constants.DeviceMetadata.OS_NAME] = "LINUX"; // Recommended by conviva
        deviceMetadata[Conviva.Constants.DeviceMetadata.OS_VERSION] = Store.deviceFirmwareVersion;
        deviceMetadata[Conviva.Constants.DeviceMetadata.CATEGORY] = Conviva.Client.DeviceCategory.SET_TOP_BOX;
        deviceMetadata[Conviva.Constants.DeviceMetadata.SCREEN_RESOLUTION_WIDTH] = 1920;
        deviceMetadata[Conviva.Constants.DeviceMetadata.SCREEN_RESOLUTION_HEIGHT] = 1080;
        deviceMetadata[Conviva.Constants.DeviceMetadata.SCREEN_RESOLUTION_SCALE_FACTOR] = 1;
        this.conviva.Analytics.setDeviceMetadata(deviceMetadata);

        this.removeEventListeners(); // in case monitoringTeardown isn't called
        this.addEventListeners();
    };

    addEventListeners = () => {
        this.listeners = [
            ...this.listeners,
            Messengers.streamReady$.addReceiver(
                this.onChannelDataReceived,
                true
            ),
            Messengers.googleDaiResponse$.addReceiver(
                this.onDaiDataReceived,
                true
            ),
            Messengers.programChange$.addReceiver(this.onProgramChange),
            Messengers.bitrateChange$.addReceiver(this.onBitrateChange, true),
            Messengers.currentAd$.addReceiver(this.onAdChange),
            Messengers.playerState$.addReceiver(this.onPlayerStateChange),
            Messengers.playerError$.addReceiver(this.onPlayerError),
            Messengers.appTeardown$.addReceiver(this.onTeardown),
        ];
    };

    removeEventListeners = () => {
        this.listeners.forEach((receiver) => receiver.unlisten());
        this.listeners = [];
    };

    onTeardown = () => {
        this.removeEventListeners();
        this.currentAd = null;
        this.adBreakCounter = 0;
        this.contentTags = {};
        this.conviva?.Analytics?.release();
    };

    onChannelDataReceived = (channelData) => {
        if (!channelData || !this.conviva) return;

        const {
            chosenProvider: chosenStreamProvider,
            conviva: channelConviva,
            programData,
        } = channelData;

        const program = programData?.program || {};

        this.streamProviderIsGoogleDAI =
            chosenStreamProvider.type === "GoogleDAIProvider";

        // Stream URL for DAI Provider will be updated in onDaiDataReceived 
        const streamUrl = this.streamProviderIsGoogleDAI
            ? ""
            : chosenStreamProvider.contentUrl;

        this.contentTags = {
            [this.conviva.Constants.STREAM_URL]: streamUrl || "N/A",
            [this.conviva.Constants.ASSET_NAME]: channelConviva.assetName || "N/A",
            [this.conviva.Constants.IS_LIVE]:
                this.conviva.Constants.StreamType.LIVE,
            [this.conviva.Constants.PLAYER_NAME]: ConvivaPlayerName,
            [this.conviva.Constants.APPLICATION_VERSION]: AppVersion,
            [this.conviva.Constants.VIEWER_ID]: channelConviva.viewerId || "N/A",
            [this.conviva.Constants.DEFAULT_RESOURCE]: channelConviva.cdn || "N/A",
            [this.conviva.Constants.FRAMEWORK_NAME]: "Google IMA",
            [this.conviva.Constants.FRAMEWORK_VERSION]: "NA",
            [this.conviva.Constants.ENCODED_FRAMERATE]: 25, // an integer value is expected

            streamUrl: streamUrl,
            assetName: channelConviva.assetName,
            "c3.cm.id": channelConviva.contentId,
            isLive: "true",
            contentType: "Live-Linear",
            playerName: ConvivaPlayerName,
            "c3.app.version": AppVersion,
            viewerId: channelConviva.viewerId,
            contentLength: channelConviva.streamDuration || "N/A",

            "c3.cm.channel": channelConviva.channel,
            "c3.cm.contentType": "Live-Linear",
            "c3.cm.categoryType": channelConviva.category || "N/A",
            "c3.cm.seriesName": program.seriesTitle || channelConviva.show,
            "c3.cm.seasonNumber": String(
                program.seasonNumber ?? channelConviva.season
            ),
            "c3.cm.showTitle":
                program.title ||
                channelConviva.episodeName ||
                channelConviva.show,
            "c3.cm.episodeNumber": String(
                program.episodeNumber ?? channelConviva.episodeNumber
            ),
            "c3.cm.genre": program.genres?.[0] || channelConviva.genre || "N/A",
            "c3.cm.genreList": channelConviva.subGenre,

            cdn: channelConviva.cdn || "N/A",
            googleDaiStreamId:
                (this.streamProviderIsGoogleDAI &&
                    chosenStreamProvider.videoId) ||
                "",
        };

        this.videoAnalytics?.setContentInfo(this.contentTags);
        this.videoAnalytics?.reportPlaybackRequested(this.contentTags);

        // https://pulse.conviva.com/learning-center/content/sensor_developer_center/sensor_integration/javascript/javascript_stream_sensor.htm?Highlight=ENCODED_FRAMERATE#5HandleUserActions
        this.conviva.Analytics?.reportDeviceMetric(
            this.conviva.Constants.Network.CONNECTION_TYPE,
            Store.deviceNetworkConnection === NetworkConnectionType.WIFI
                ? "WiFi"
                : "Ethernet"
        );
    };

    onDaiDataReceived = (daiResponse) => {
        if (!daiResponse || !this.conviva) return;

        // Update streamUrl with the manifest url
        this.contentTags[this.conviva.Constants.STREAM_URL] = daiResponse.stream_manifest || "N/A";
        this.videoAnalytics?.setContentInfo(this.contentTags);
    };

    onProgramChange = (programData) => {
        if (!programData) return;

        const program = programData?.program || {};
        console.log("[acc] Conviva program changed to", program?.seriesTitle);

        const { start, end } = programData?.availability || {};
        const durationInMs = (new Date(end).getTime()) - (new Date(start).getTime());
        if (durationInMs > 0) {
             // Update with program duration (in seconds)
            const durationInSec = ~~(durationInMs / 1000);
            this.contentTags[this.conviva.Constants.DURATION] = durationInSec;
        }

        this.contentTags["c3.cm.seriesName"] = program.seriesTitle;
        this.contentTags["c3.cm.seasonNumber"] = String(
            program.seasonNumber ?? ""
        );
        this.contentTags["c3.cm.showTitle"] = program.title;
        this.contentTags["c3.cm.episodeNumber"] = String(
            program.episodeNumber ?? ""
        );
        this.contentTags["c3.cm.genre"] = program.genres?.[0] || "N/A";
        this.videoAnalytics?.setContentInfo(this.contentTags);
    };

    onBitrateChange = (bitrateBps, framerate) => {
        /*
            For this event we only have these available data:
                Event Payload:
                    sessionId
                    time
                    bitRate
                    description
                    width
                    height
                    framerate
                    position
                    cappedProfile
                    displayWidth
                    displayHeight
                Documentation info from - https://code.rdkcentral.com/r/plugins/gitiles/rdk/components/generic/aamp/+/refs/heads/stable2/AAMP-UVE-API.md#universal-video-engine-uve-events-bitratechanged
        */
        if (bitrateBps) {
            console.log("[acc] bitrate change in conviva", bitrateBps);
            this.videoAnalytics?.reportPlaybackMetric(
                this.conviva?.Constants.Playback.BITRATE,
                bitrateBps / 1000
            );
        }
        if (framerate) {
            console.log("[acc] report framerate (RENDERED_FRAMERATE) to conviva", framerate);
            this.videoAnalytics?.reportPlaybackMetric(
                this.conviva?.Constants.Playback.RENDERED_FRAMERATE,
                framerate
            );
        }
    };

    onAdChange = (ad) => {
        const prevAd = this.currentAd;
        this.currentAd = ad;

        if (!this.conviva) return;

        if (!prevAd && this.currentAd) {
            // AD break start
            const adType = this.conviva.Constants.AdType.SERVER_SIDE;

            const adAttributes = {};
            this.adBreakCounter += 1;

            // values only show up in touchstone if they are strings
            adAttributes.podPosition =
                this.conviva.Constants.AdPosition.MIDROLL;
            adAttributes.podIndex = this.adBreakCounter.toString();
            adAttributes.absoluteIndex = "-1";
            adAttributes["c3.ad.system"] = "NA";
            adAttributes["c3.ad.adStitcher"] = this.streamProviderIsGoogleDAI
                ? "Google DAI"
                : "";
            adAttributes["c3.ad.technology"] = "Server Side";
            if (ad) {
                adAttributes.podDuration = String(ad.adBreakDuration || -1);
                adAttributes["c3.ad.id"] = String(ad.adBreakId || "empty");
                adAttributes["c3.ad.position"] = "Mid-roll";
                adAttributes["c3.ad.isSlate"] = String(!!ad.isSlate);
                adAttributes["c3.ad.firstAdSystem"] = "NA";
                adAttributes["c3.ad.firstAdId"] = "NA";
                adAttributes["c3.ad.firstCreativeId"] = "NA";
            }

            this.videoAnalytics?.reportAdBreakStarted(
                adType,
                this.conviva.Constants.AdPlayer.CONTENT,
                adAttributes
            );
        } else if (prevAd && !this.currentAd) {
            this.videoAnalytics?.reportAdBreakEnded();
            this.videoAnalytics?.reportPlaybackMetric(
                this.conviva.Constants.Playback.PLAYER_STATE,
                this.conviva.Constants.PlayerState.PLAYING
            );
        }
    };

    onPlayerStateChange = (state) => {
        if (!this.currentAd) {
            switch (state) {
                case PlayerStates.Buffering:
                    this.videoAnalytics?.reportPlaybackMetric(
                        this.conviva?.Constants.Playback.PLAYER_STATE,
                        this.conviva?.Constants.PlayerState.BUFFERING
                    );
                    break;
                case PlayerStates.Stopped:
                case PlayerStates.Ended:
                    this.videoAnalytics?.reportPlaybackMetric(
                        this.conviva?.Constants.Playback.PLAYER_STATE,
                        this.conviva?.Constants.PlayerState.STOPPED
                    );
                    break;
                case PlayerStates.Paused:
                    this.videoAnalytics?.reportPlaybackMetric(
                        this.conviva?.Constants.Playback.PLAYER_STATE,
                        this.conviva?.Constants.PlayerState.PAUSED
                    );
                    break;
                case PlayerStates.Playing:
                    this.videoAnalytics?.reportPlaybackMetric(
                        this.conviva?.Constants.Playback.PLAYER_STATE,
                        this.conviva?.Constants.PlayerState.PLAYING
                    );
                    break;
            }
        }
    };

    onPlayerError = (errorData) => {
        try {
            // always seems to be fatal with this AAMP event
            const isFatal = true;
            const errorMessage = errorData?.message;
            this.videoAnalytics?.reportPlaybackError(
                errorMessage,
                this.conviva?.Constants.ErrorSeverity[
                    isFatal ? "FATAL" : "WARNING"
                ]
            );
            this.videoAnalytics?.reportPlaybackFailed(
                `Code ${errorData?.code} ${errorMessage}`
            );
        } catch (err) {
            console.error(err);
        }
    };
}

export default new ConvivaBridge();
