import { getProgramInfo } from "./sbs-api";
import { Messengers } from "../helpers/Messengers";

let programCheckTimeout = null;
let currentProgramData = null;
let currentMpxID = null;
const DEFAULT_PROGRAM_POLL_MS = 60 * 1000;

const getTimeTillProgramEndMs = (programData) => {
    try {
        let remainingTimeSeconds = -1;

        const { start, end } = programData.availability || {};
        const nowMs = Date.now();
        const startRemainingSeconds =
            Math.round((new Date(start).getTime() - nowMs) / 1000) + 1;
        const endRemainingSeconds = Math.round(
            (new Date(end).getTime() - nowMs) / 1000
        );

        if (
            startRemainingSeconds >= 0 &&
            (remainingTimeSeconds > startRemainingSeconds ||
                remainingTimeSeconds === -1)
        ) {
            remainingTimeSeconds = startRemainingSeconds;
        }

        if (
            endRemainingSeconds >= 0 &&
            (remainingTimeSeconds > endRemainingSeconds ||
                remainingTimeSeconds === -1)
        ) {
            remainingTimeSeconds = endRemainingSeconds;
        }

        return remainingTimeSeconds > 0
            ? remainingTimeSeconds * 1000
            : remainingTimeSeconds;
    } catch (err) {
        console.warn("[acc] Could not parse program time, ", err);
        return -1;
    }
};

const checkProgramChangedAfterMs = (timeMs) => {
    // add arbitrary 5 second to increase likelihood we're actually within next program bounds
    const checkTimeoutLengthMs =
        timeMs > 0 ? timeMs + 5 * 1000 : DEFAULT_PROGRAM_POLL_MS;
    console.log(
        `[acc] Will check for program change in ${timeMs / 1000} seconds`
    );
    if (programCheckTimeout) {
        clearTimeout(programCheckTimeout);
    }
    programCheckTimeout = window.setTimeout(async () => {
        try {
            const updatedProgramData = await getProgramInfo(currentMpxID);

            if (
                updatedProgramData &&
                updatedProgramData.program.id !== currentProgramData?.program.id
            ) {
                currentProgramData = updatedProgramData;
                Messengers.programChange$.send(currentProgramData);
                checkProgramChangedAfterMs(
                    getTimeTillProgramEndMs(currentProgramData)
                );
            } else {
                console.log("[acc] program not changed or no program exists");
                checkProgramChangedAfterMs(DEFAULT_PROGRAM_POLL_MS);
            }
        } catch (err) {
            console.warn("[acc] Error polling program change", err);
            checkProgramChangedAfterMs(DEFAULT_PROGRAM_POLL_MS);
        }
    }, checkTimeoutLengthMs);
};

const onStreamReady = async (channel) => {
    if (!channel) return;

    currentMpxID = channel.mpxMediaId;
    currentProgramData = channel.programData;

    const timeTillEndMs = getTimeTillProgramEndMs(currentProgramData);
    checkProgramChangedAfterMs(timeTillEndMs);
};

Messengers.streamReady$.addReceiver(onStreamReady);
Messengers.appTeardown$.addReceiver(() => {
    if (programCheckTimeout) {
        clearTimeout(programCheckTimeout);
    }
});
