import { Store } from "../helpers/sbs-store";
import { ErrorTypes, MLEError } from "../../provider-utils";
import { Messengers } from "../helpers/Messengers";
import {
    isValidChannelResponse,
    isValidGoogleDAIResponse,
} from "./sbs-validators";

class FetchError extends Error {
    status;
    constructor(message, status) {
        super(message);
        this.status = status;
        Object.setPrototypeOf(this, FetchError.prototype);
    }
}

const AppVersion = Store.appVersion;

const baseUrl = Store.baseApiUrl;
const catalogueBaseUrl = Store.catalogueUrl;

const getDAIBody = (adTag) => {
    const customParamsString = Object.keys(adTag.customParameters)
        .map(
            (key) => `${key}=${encodeURIComponent(adTag.customParameters[key])}`
        )
        .join("&");

    const targetingParams = {
        iu: adTag.iu,
        sid: Messengers.oztamSessionId$.getCurrentValue(),
        cust_params: encodeURIComponent(customParamsString),
        description_url: encodeURIComponent(adTag.description_url),
        npa: String(0),
        ppid: encodeURIComponent(adTag.ppid),
        url: encodeURIComponent(adTag.description_url),
    };

    const daiString = Object.keys(targetingParams)
        .map((key) => `${key}=${targetingParams[key]}`)
        .join("&");

    return daiString;
};

const tryGoogleProvider = async (daiProvider, adTag) => {
    try {
        const daiString = getDAIBody(adTag);

        const daiUrl = `https://dai.google.com/linear/v1/hls/event/${daiProvider.videoId}/stream`;
        const daiResponse = await fetch(daiUrl, {
            method: "POST",
            headers: {
                "content-type": "application/x-www-form-urlencoded",
            },
            body: daiString,
        })
            .then((res) => {
                if (res.ok) return res;

                throw new FetchError(res.statusText, res.status);
            })
            .then((res) => res.json());

        if (isValidGoogleDAIResponse(daiResponse)) {
            Messengers.googleDaiResponse$.send(daiResponse);
            return daiResponse.stream_manifest;
        } else {
            throw new Error("Invalid DAI response");
        }
    } catch (err) {
        let message = err.message;
        if (err instanceof Error) {
            if (err.message === "Invalid DAI response") {
                err.message = "No stream manifest url returned by google DAI";
            } else {
                err.message =
                    "Generic error requesting google DAI or parsing ad tag to make request";
            }
        }
        throw new MLEError(message, ErrorTypes.PROVIDER, err.status);
    }
};

export const getSourceUrl = async (mpxID) => {
    const authHeader = "Basic " + btoa(Store.sessionToken + ":tv");

    const deviceId = Store.deviceId;
    const platform = "Xi1"; // hardcode to match On Demand app until Firebolt gets sunsetted;
    const uniqueId = `${deviceId}-3.20.0-${Date.now()}`;
    let channelApiEndpoint = `${baseUrl}/api/v3/video_stream?id=${mpxID}&context=tv&make=${Store.deviceMake}&model=${platform}&version=3.20.0&uniqueid=${uniqueId}&deviceadid=${deviceId}&deviceadidoptout=0`;

    // DE-MUXED streams: audio=demuxed in query string
    //    without audio=demuxed query string parameter
    //        -> we are getting a muxed stream
    //    with audio=demuxed query string parameter
    //        -> we are getting a de-muxed stream instead
    //        ... in case de-muxed stream does not exist for a specific asset, API will still return a muxed stream
    channelApiEndpoint += `&audio=demuxed`;
    console.log("[acc] Requesting demuxed stream:", channelApiEndpoint);

    const channelDataPromise = async () => {
        const fetchResponse = await fetch(channelApiEndpoint, {
            headers: {
                Authorization: authHeader,
            },
        });
        if (!fetchResponse.ok)
            throw new FetchError(
                fetchResponse.statusText,
                fetchResponse.status
            );
        return await fetchResponse.json();
    };

    let channelData;
    let programData;
    try {
        const [channelApiResponse, program] = await Promise.all([
            channelDataPromise(),
            getProgramInfo(mpxID),
        ]);

        if (!isValidChannelResponse(channelApiResponse)) {
            throw new Error("Invalid channel response");
        }
        channelData = channelApiResponse;
        if (channelData.error) {
            if (channelData.error === "You must be signed in") {
                throw new Error("Not Logged In");
            } else if (channelData.error === "not found") {
                throw new Error("MpxID not found on SBS API");
            } else {
                throw new Error(channelData.error);
            }
        }
        programData = program;
        const memberId =
            channelData.adTag.customParameters?.uid ||
            channelData.conviva?.viewerId;
        if (memberId) {
            Store.memberId = memberId;
        }
    } catch (err) {
        let type = ErrorTypes.PROVIDER;
        let message = "Generic error requesting TV channel " + err.message;
        let status = err instanceof FetchError ? err.status : undefined;
        if (err.message === "Not Logged In") {
            (message = "SBS auth token is invalid"), (type = ErrorTypes.AUTH);
        }
        if (err.message === "MpxID not found on SBS API") {
            type = ErrorTypes.CHANNEL_MAP;
            message = err.message;
        }
        if (err.message === "Api Return Error") {
            message =
                "Error returned by TV Channel response in JSON (not signed in, etc)";
        } else if (err.message === "Missing Provider") {
            message = "No GoogleDAIProvider object on TV Channel response";
        } else if (
            err.message === "Failed to fetch" ||
            err.message?.includes("Allow-Origin")
        ) {
            message = "CORS or network error requesting TV channel";
            status = 0;
        }
        throw new MLEError(message, type, status);
    }

    try {
        // nested try catches :(
        let foundError = new Error("Empty Providers");
        if (channelData.streamProviders?.length) {
            // goal is to try each in order, if it fails move to next.
            for (const provider of channelData.streamProviders) {
                if (provider.type === "GoogleDAIProvider") {
                    try {
                        Messengers.streamReady$.send({
                            ...channelData,
                            mpxMediaId: mpxID,
                            chosenProvider: provider,
                            programData,
                        });
                        return await tryGoogleProvider(
                            provider,
                            channelData.adTag
                        );
                    } catch (err) {
                        foundError = err;
                    }
                    break;
                } else if (
                    provider.videoEncoding === "hls" &&
                    provider.contentUrl
                ) {
                    Messengers.streamReady$.send({
                        ...channelData,
                        mpxMediaId: mpxID,
                        chosenProvider: provider,
                        programData,
                    });
                    return provider.contentUrl;
                }
            }
        }
        // should have returned a url by now, time to error
        throw foundError;
    } catch (err) {
        let status = err instanceof FetchError ? err.status : undefined;
        let message =
            "Generic error requesting google DAI or parsing ad tag to make reqest";
        if (err.message === "No Manifest") {
            message = "No stream manifest url returned by google DAI";
        } else if (err.message === "Empty Providers") {
            message = "Could not find vavlid stream provider in API response";
        }
        throw new MLEError(message, ErrorTypes.PROVIDER, status);
    }
};

export const getProgramInfo = async (mpxId) => {
    try {
        const liveResponse = await fetch(Store.tvEpgUrl);
        if (!liveResponse.ok) {
            throw new Error(liveResponse);
        }
        const liveJson = await liveResponse.json();

        const matchingChannel = liveJson.items.find(
            (item) => String(item.channel.mpxMediaID) === String(mpxId)
        );
        if (!matchingChannel)
            throw new Error("No matching channel when getting program info");

        const matchingEntry = matchingChannel.entries.find((entry) => {
            const { start, end } = entry.availability || {};
            const nowMs = Date.now();
            return (
                nowMs > new Date(start).getTime() &&
                nowMs < new Date(end).getTime()
            );
        });
        return matchingEntry;
    } catch (err) {
        console.warn("[acc] Error retrieving live program data", err);
        return null;
    }
};
