export const isValidGoogleDAIResponse = (response) => {
    return typeof response === "object" && !!response?.stream_manifest;
};

export const isValidChannelResponse = (response) => {
    return !!(
        typeof response === "object" &&
        (response?.error ||
            response.oztam ||
            response.adTag ||
            response.streamProviders)
    );
};
